import React from 'react';
import '../../App.css';
import './WhoWeAre.css';
import garbaImage from "../../images/garba.jpg";

function WhoWeAre() {
    console.log("function called.");
    return (
        <>
            <h1 className="who-we-are">OUR STORY</h1>
            <div className="our-story-container">
                    <img src={garbaImage} alt="Central Zone Navratri" className="our-story-image" />
                    <div className="our-story-information">
                        <p>The Warwick Hindu Society is one of x societies which make up the NHSF (National Hindu
                            Students’ Forum) - a safe space for Hindu university students across the UK to explore
                            and identify with their religion and spirituality.<br/><br/>

                            From its creation through several London university students in 1991 to now (over 4000
                            active members later), the NHSF has established itself as a means of allowing students
                            to participate in weekly Aartis (discussions on varied and meaningful topics), sewa
                            (charity work) events, sports competitions and flagship cultural events. Its main aim is
                            to ‘Create a home away from home’ - one which it regularly does through providing a space
                            for students to relate, bond and cultivate lifelong friendships.<br/><br/>

                            As Warwick Hindu Society, we make sure we provide as much care and support to our students
                            as possible - from our flagship Navratri and Holi events to regular discussions and fun
                            socials (including laser tag, bowling, dinners and much more). Make sure to contact our exec
                            members if you have any questions or suggestions for what more we could do for you in the
                            future!<br/><br/>


                        </p>
                    </div>
            </div>
        </>

    )
}


export default WhoWeAre;
